"use client";

import { useEffect, useState } from "react";
import Header from "@/src/components/landingpage/Header";
import Footer from "@/src/components/landingpage/Footer";
import UserTopMenu from "@/src/components/landingpage/UserTopMenu";
import NotificationDialog from "@/src/components/ui/NotificationDialog";
import { useUser } from "@/src/context/AuthContext";
import { useVehicleIssues } from "@/src/hooks/useVehicleIssues";
import { useTechnicianMap } from "@/src/hooks/useTechnicianMap";
import { ExtendedVehicleIssue, VehicleIssueStatus } from "@/src/lib/vehicleIssues/vehicleIssueTypes";
import { Button } from "@/src/components/ui/button";
import VehicleIssueTable from "@/src/components/vehicleIssues/VehicleIssueTable";
import AssignTechnicianForm from "@/src/components/vehicleIssues/AssignTechnicianForm";

// Helper: Format currency (VND)
function formatCurrency(value: number) {
  return value.toLocaleString("vi-VN", {
    style: "currency",
    currency: "VND",
    minimumFractionDigits: 0,
  });
}


// Helper: Get status color
function getStatusColor(status: VehicleIssueStatus) {
  switch (status) {
    case "pending":
      return "text-gray-500";
    case "assigned":
      return "text-blue-500";
    case "proposed":
      return "text-yellow-500";
    case "confirmed":
      return "text-green-600";
    case "rejected":
      return "text-red-500";
    case "in_progress":
      return "text-indigo-500";
    case "resolved":
      return "text-green-700";
    case "closed":
      return "text-gray-400";
    default:
      return "text-gray-600";
  }
}

export default function VehicleIssuesManagementPage() {
  const { role, companyId, loading: userLoading } = useUser();
  const normalizedRole = role?.toLowerCase();
  const isAdmin = normalizedRole === "admin";

  const canViewIssues = isAdmin || (!!companyId && ["company_owner", "company_admin", "technician"].includes(normalizedRole || ""));

  const [editingIssue, setEditingIssue] = useState<ExtendedVehicleIssue | null>(null);
  const [showForm, setShowForm] = useState(false);
  const [dialog, setDialog] = useState({
    open: false,
    type: "info" as "success" | "error" | "info",
    title: "",
    description: "",
  });

  const { technicianMap, loading: technicianMapLoading } = useTechnicianMap(companyId ?? undefined);
  const { issues, loading: issuesLoading, updateIssue } = useVehicleIssues({
    role: role ?? undefined,
    companyId: companyId ?? undefined,
  });

  const loading = userLoading || technicianMapLoading || issuesLoading;

  const showDialog = (type: "success" | "error" | "info", title: string, description = "") => {
    setDialog({ open: true, type, title, description });
  };

  useEffect(() => {
    if (dialog.open) {
      const timer = setTimeout(() => setDialog((prev) => ({ ...prev, open: false })), 3000);
      return () => clearTimeout(timer);
    }
  }, [dialog.open]);

  const handleAssignTechnician = async (userId: string) => {
    if (!editingIssue) return;

    try {
      await updateIssue(editingIssue.id, {
        assignedTo: userId,
        assignedAt: new Date() as any,
        status: "assigned",
      } as any);

      showDialog("success", "Technician assigned successfully");
      setShowForm(false);
      setEditingIssue(null);
    } catch {
      showDialog("error", "Failed to assign technician");
    }
  };

  const handleUpdateStatus = async (issue: ExtendedVehicleIssue, newStatus: VehicleIssueStatus) => {
    await updateIssue(issue.id, { status: newStatus });
    showDialog("success", `Issue status updated to ${newStatus}`);
  };

  if (loading) return <div className="text-center py-10">‚è≥ Loading issues and technician data...</div>;
  if (!canViewIssues) return <div className="text-center py-10 text-red-500">üö´ You do not have permission to view this page.</div>;

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />
      <main className="flex-1 p-6 space-y-6">
        <h1 className="text-2xl font-bold">üõ†Ô∏è Vehicle Issues Management</h1>

        <div className="overflow-auto border rounded-xl">
          <table className="min-w-full text-sm">
            <thead>
              <tr className="bg-gray-100 text-left">
                <th className="p-2">VIN</th>
                <th className="p-2">Plate</th>
                <th className="p-2">Issue Type</th>
                <th className="p-2">Description</th>
                <th className="p-2">Status</th>
                <th className="p-2">Company</th>
                <th className="p-2">Station</th>
                <th className="p-2">Reported By</th>
                <th className="p-2">Reported At</th>
                <th className="p-2">Proposed Solution</th>
                <th className="p-2">Proposed Cost</th>
                <th className="p-2">Actions</th>
              </tr>
            </thead>
            <tbody>
              {[...issues].sort((a, b) => (b.reportedAt?.toDate().getTime() ?? 0) - (a.reportedAt?.toDate().getTime() ?? 0))
                .map(issue => {
                  const disableAssign = ["proposed", "confirmed", "rejected", "in_progress", "resolved", "closed"].includes(issue.status);

                  return (
                    <tr key={issue.id} className="border-t hover:bg-gray-50">
                      <td className="p-2">{issue.vin}</td>
                      <td className="p-2">{issue.plateNumber}</td>
                      <td className="p-2">{issue.issueType}</td>
                      <td className="p-2">{issue.description}</td>
                      <td className={`p-2 font-semibold ${getStatusColor(issue.status)}`}>{issue.status}</td>
                      <td className="p-2">{issue.companyName}</td>
                      <td className="p-2">{issue.stationName}</td>
                      <td className="p-2">{issue.reportedBy}</td>
                      <td className="p-2">{issue.reportedAt?.toDate().toLocaleString()}</td>
                      <td className="p-2">{issue.proposedSolution || "-"}</td>
                      <td className="p-2">{issue.proposedCost ? formatCurrency(issue.proposedCost) : "-"}</td>
                      <td className="p-2 space-x-2">
                        {issue.status === "proposed" && (normalizedRole === "company_owner" || normalizedRole === "company_admin" || isAdmin) && (
                          <>
                            <Button className="bg-green-500 text-white hover:bg-green-600" onClick={() => handleUpdateStatus(issue, "confirmed")}>
                              Approve
                            </Button>
                            <Button variant="outline" className="mt-2 text-red-600 border-red-400 hover:bg-red-50" onClick={() => handleUpdateStatus(issue, "rejected")}>
                              Reject
                            </Button>
                          </>
                        )}

                        {issue.status !== "proposed" && (
                          <Button
                            variant="ghost"
                            disabled={disableAssign}
                            onClick={() => {
                              setEditingIssue(issue);
                              setShowForm(true);
                            }}
                          >
                            Assign
                          </Button>
                        )}
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>

        {showForm && editingIssue && (
          <div className="bg-white border rounded-xl shadow p-6 space-y-6">
            <h2 className="text-2xl font-bold">Assign Technician</h2>
            <AssignTechnicianForm companyId={companyId || ""} onAssign={handleAssignTechnician} />
            <div className="flex justify-end">
              <Button variant="ghost" onClick={() => { setShowForm(false); setEditingIssue(null); }}>
                Cancel
              </Button>
            </div>
          </div>
        )}
      </main>
      <Footer />
      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog(prev => ({ ...prev, open: false }))}
      />
    </div>
  );
}
