import { useEffect, useState } from "react";
import { db } from "@/src/firebaseConfig";
import { collection, onSnapshot, query, where, doc, updateDoc, getDocs } from "firebase/firestore";
import { ExtendedVehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";

interface Options {
  role?: string;
  companyId?: string;
  technicianUserId?: string;
}

export function useVehicleIssues(options?: Options) {
  const [issues, setIssues] = useState<ExtendedVehicleIssue[]>([]);
  const [rawIssues, setRawIssues] = useState<ExtendedVehicleIssue[]>([]);
  const [loading, setLoading] = useState(true);
  const [technicianMap, setTechnicianMap] = useState<Record<string, string>>({});

  const normalizedRole = options?.role?.toLowerCase();
  const isAdmin = normalizedRole === "admin";
  const companyId = options?.companyId;
  const technicianUserId = options?.technicianUserId;

  // Load Technician Map (d√†nh cho Admin ho·∫∑c company views)
  useEffect(() => {
    // Admin th√¨ load to√†n b·ªô staffs, Company th√¨ load theo companyId
    if (!companyId && !isAdmin) return;

    const fetchTechnicians = async () => {
      const q = isAdmin
        ? query(collection(db, "staffs"))
        : query(collection(db, "staffs"), where("companyId", "==", companyId!));

      const snapshot = await getDocs(q);
      const map: Record<string, string> = {};

      snapshot.docs.forEach(doc => {
        const data = doc.data();
        if (data.userId) {
          map[data.userId] = data.name || "Unnamed Technician";
        }
      });

      setTechnicianMap(map);
    };

    fetchTechnicians();
  }, [companyId, isAdmin]);

  // Load Vehicle Issues (theo role)
  useEffect(() => {
    setLoading(true);

    // Kh√¥ng c√≥ quy·ªÅn (ch∆∞a ƒë·ªß d·ªØ ki·ªán) ‚Üí reset data
    if (!isAdmin && !companyId && !technicianUserId) {
      setRawIssues([]);
      setIssues([]);
      setLoading(false);
      return;
    }

    let q;

    if (isAdmin) {
      q = query(collection(db, "vehicleIssues")); // Admin l·∫•y t·∫•t
    } else if (technicianUserId) {
      q = query(collection(db, "vehicleIssues"), where("assignedTo", "==", technicianUserId)); // Technician ‚Üí assignedTo
    } else if (companyId) {
      q = query(collection(db, "vehicleIssues"), where("companyId", "==", companyId)); // Company ‚Üí theo companyId
    } else {
      // Safety fallback
      setRawIssues([]);
      setIssues([]);
      setLoading(false);
      return;
    }

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const fetched: ExtendedVehicleIssue[] = snapshot.docs.map(doc => ({
        ...(doc.data() as ExtendedVehicleIssue),
        id: doc.id,
      }));

      setRawIssues(fetched);
      setLoading(false);
    });

    return () => unsubscribe();
  }, [companyId, technicianUserId, normalizedRole]);

  // Process rawIssues + TechnicianMap ‚Üí Hi·ªÉn th·ªã assignedToName
  useEffect(() => {
    const processed = rawIssues.map(issue => ({
      ...issue,
      assignedToName: issue.assignedTo
        ? technicianMap[issue.assignedTo] ?? issue.assignedTo
        : undefined,
    }));

    setIssues(processed);
  }, [rawIssues, technicianMap]);

  const updateIssue = async (id: string, data: Partial<ExtendedVehicleIssue>) => {
    await updateDoc(doc(db, "vehicleIssues", id), data);
  };

  return { issues, loading, updateIssue };
}
